import React, { useState, useEffect } from 'react';
import { FaShoppingBag, FaTrash, FaArrowLeft, FaCreditCard } from 'react-icons/fa';
import { motion, AnimatePresence } from 'framer-motion';

const Cart = ({ cart, removeFromCart }) => {
  const [isRemoving, setIsRemoving] = useState(null);
  const [notification, setNotification] = useState(null);
  
  const total = cart.reduce((sum, item) => sum + item.price, 0);
  const itemCount = cart.length;

  const handleRemoveItem = async (itemId) => {
    setIsRemoving(itemId);
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 400));
    
    const item = cart.find(item => item.id === itemId);
    removeFromCart(itemId);
    
    // Show notification
    setNotification({
      message: `${item.name} removed from cart`,
      type: 'error'
    });
    
    // Hide notification after 3 seconds
    setTimeout(() => {
      setNotification(null);
    }, 3000);
  };

  const cartItemVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: { 
      opacity: 1, 
      x: 0,
      transition: { 
        type: 'spring',
        stiffness: 300,
        damping: 20
      }
    },
    exit: { 
      opacity: 0, 
      x: 20,
      transition: { duration: 0.2 }
    }
  };

  return (
    <div className="cart-page">
      <div className="cart-header">
        <h2>Your Shopping Cart</h2>
        {itemCount > 0 && (
          <span className="item-count">{itemCount} {itemCount === 1 ? 'item' : 'items'}</span>
        )}
      </div>
      
      {notification && (
        <div className={`notification ${notification.type} show`}>
          <div className="notification-content">
            {notification.message}
          </div>
        </div>
      )}
      
      {cart.length === 0 ? (
        <div className="empty-cart">
          <div className="empty-cart-icon">
            <FaShoppingBag />
          </div>
          <h3>Your cart is empty</h3>
          <p>Looks like you haven't added anything to your cart yet</p>
          <a href="/products" className="btn btn-primary">
            <FaArrowLeft style={{ marginRight: '8px' }} />
            Continue Shopping
          </a>
        </div>
      ) : (
        <>
          <AnimatePresence>
            <div className="cart-items">
              {cart.map((item, index) => (
                <motion.div
                  key={`${item.id}-${index}`}
                  className="cart-item"
                  variants={cartItemVariants}
                  initial="hidden"
                  animate="visible"
                  exit="exit"
                  layout
                >
                  <div className="cart-item-content">
                    <img 
                      src={item.image} 
                      alt={item.name} 
                      className="cart-item-image" 
                    />
                    <div className="cart-item-details">
                      <h4>{item.name}</h4>
                      <p className="item-price">{new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR' }).format(item.price)}</p>
                      <p className="item-description">{item.description}</p>
                    </div>
                  </div>
                  <button
                    className="remove-item"
                    onClick={() => handleRemoveItem(item.id)}
                    disabled={isRemoving === item.id}
                    aria-label="Remove item"
                  >
                    {isRemoving === item.id ? (
                      <div className="spinner" />
                    ) : (
                      <FaTrash />
                    )}
                  </button>
                </motion.div>
              ))}
            </div>
          </AnimatePresence>
          
          <motion.div 
            className="cart-summary"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2 }}
          >
            <div className="summary-row">
              <span>Subtotal</span>
              <span>{new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR' }).format(total)}</span>
            </div>
            <div className="summary-row">
              <span>Shipping</span>
              <span>Free</span>
            </div>
            <div className="summary-row total">
              <span>Total</span>
              <span>{new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR' }).format(total)}</span>
            </div>
            
            <button className="btn btn-primary checkout-btn">
              <FaCreditCard style={{ marginRight: '8px' }} />
              Proceed to Checkout
            </button>
            
            <div className="secure-checkout">
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <rect x="3" y="11" width="18" height="11" rx="2" ry="2"></rect>
                <path d="M7 11V7a5 5 0 0 1 10 0v4"></path>
              </svg>
              <span>Secure Checkout</span>
            </div>
          </motion.div>
        </>
      )}
      
      <style jsx>{`
        .cart-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 2rem;
        }
        
        .item-count {
          background: var(--primary-color);
          color: white;
          padding: 0.25rem 0.75rem;
          border-radius: 20px;
          font-size: 0.85rem;
          font-weight: 600;
        }
        
        .empty-cart {
          text-align: center;
          padding: 3rem 1rem;
          background: white;
          border-radius: var(--border-radius);
          box-shadow: var(--box-shadow);
        }
        
        .empty-cart-icon {
          font-size: 4rem;
          color: var(--secondary-color);
          margin-bottom: 1.5rem;
        }
        
        .empty-cart h3 {
          margin-bottom: 0.5rem;
          color: var(--primary-dark);
        }
        
        .empty-cart p {
          color: #666;
          margin-bottom: 1.5rem;
        }
        
        .cart-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background: white;
          padding: 1.25rem;
          margin-bottom: 1rem;
          border-radius: var(--border-radius);
          box-shadow: var(--box-shadow);
        }
        
        .cart-item-content {
          display: flex;
          align-items: center;
          flex: 1;
        }
        
        .cart-item-image {
          width: 80px;
          height: 80px;
          object-fit: cover;
          border-radius: 8px;
          margin-right: 1.5rem;
        }
        
        .cart-item-details {
          flex: 1;
        }
        
        .cart-item h4 {
          margin: 0 0 0.5rem 0;
          color: var(--primary-dark);
        }
        
        .item-price {
          font-weight: 600;
          color: var(--accent-color);
          margin: 0.25rem 0;
        }
        
        .item-description {
          color: #666;
          font-size: 0.9rem;
          margin: 0.25rem 0 0 0;
        }
        
        .remove-item {
          background: none;
          border: none;
          color: #999;
          cursor: pointer;
          padding: 0.5rem;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all 0.2s ease;
        }
        
        .remove-item:hover {
          background: #f5f5f5;
          color: #ff4444;
        }
        
        .spinner {
          width: 16px;
          height: 16px;
          border: 2px solid rgba(0,0,0,0.1);
          border-radius: 50%;
          border-top-color: var(--primary-color);
          animation: spin 1s ease-in-out infinite;
        }
        
        .cart-summary {
          background: white;
          border-radius: var(--border-radius);
          padding: 1.5rem;
          box-shadow: var(--box-shadow);
          margin-top: 2rem;
        }
        
        .summary-row {
          display: flex;
          justify-content: space-between;
          margin-bottom: 0.75rem;
          padding-bottom: 0.75rem;
          border-bottom: 1px solid #eee;
        }
        
        .summary-row:last-child {
          border-bottom: none;
          margin-bottom: 0;
          padding-bottom: 0;
        }
        
        .summary-row.total {
          font-size: 1.25rem;
          font-weight: 600;
          margin: 1.5rem 0;
          padding: 1rem 0;
          border-top: 2px solid #eee;
          border-bottom: none;
        }
        
        .checkout-btn {
          width: 100%;
          padding: 1rem;
          font-size: 1.1rem;
          font-weight: 600;
          margin-top: 1rem;
        }
        
        .secure-checkout {
          display: flex;
          align-items: center;
          justify-content: center;
          color: #666;
          font-size: 0.85rem;
          margin-top: 1rem;
        }
        
        .secure-checkout svg {
          margin-right: 0.5rem;
        }
        
        @keyframes spin {
          to { transform: rotate(360deg); }
        }
      `}</style>
    </div>
  );
};

export default Cart;
